import { Component, OnInit } from '@angular/core';
import { NavParams, ModalController } from '@ionic/angular';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { PlatformLocation } from '@angular/common';
import { RequestService } from 'src/app/api/request.service';
import { Model } from 'src/app/api/models/model';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ChatService } from 'src/app/services/chat.service';

@Component({
  selector: 'app-add-result',
  templateUrl: './add-result.page.html',
  styleUrls: ['./add-result.page.scss'],
})
export class AddResultPage extends ModalForm implements OnInit , ModalFormInterface {


  serviceGame : Model;
  game :any;
  numGoals = [] ;
  status = 'Jugado'

  constructor(
    public navParms : NavParams,
    public modalController : ModalController,
    location : PlatformLocation,
    public requestService : RequestService,
    public dialogService : DialogService,
    public chatService : ChatService
  ) {
    super(location,modalController)

    this.serviceGame = new Model('Game',requestService);

    this.game = {... navParms.get('game') }
  
    
    for (var i = 0; i <= 30; i++) {
      this.numGoals.push(i);
    }
    console.log(this.game)
   }


  initObject() {
    
  }

  async confirm(){

    let msj = `Ten presente que al ${this.status} este partido ya no podrás modificar los datos, solo el administrador general podrá hacerlo.`;
    this.dialogService.presentAlertConfirm('Confirmar',msj,() =>{
      this.submit();
    })
  }

  async submit(){
    this.game.status = this.status;

    await this.dialogService.loadingProgres();

    console.log('submit');
    this.serviceGame.api_update(this.game).subscribe((respose) => {
      this.dialogService.dimissLoaging();
      if(respose['status'] == 'success'){
        this.dimiss(this.game);
      }
    },
    error => {
      this.dialogService.dimissLoaging();
    },()=>{
      this.dialogService.dimissLoaging();
    })
  }


  dimiss(game){
    this.modalController.dismiss({
      game : game
    })
  }

  close() {
    this.modalController.dismiss();
  }

  log(event){
    console.log(event)
  }



}
