import { Component, OnInit } from '@angular/core';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { Model } from 'src/app/api/models/model';
import { NavParams, ModalController } from '@ionic/angular';
import { PlatformLocation } from '@angular/common';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import * as moment from 'moment';
import { UtilService } from 'src/app/services/util.service';

@Component({
  selector: 'app-form-expense',
  templateUrl: './form-expense.page.html',
  styleUrls: ['./form-expense.page.scss'],
})
export class FormExpensePage extends ModalForm implements OnInit, ModalFormInterface {

  public team_id ;
  public expense : any;
  public modelExpense: Model;


  constructor(
    public navParms : NavParams,
    public modalController : ModalController,
    location : PlatformLocation,
    private requestService : RequestService,
    private dialogService : DialogService,
    public authUser : AuthUserService,
    public util : UtilService
  ) {
    super(location,modalController)

    this.team_id = navParms.get('team_id');

    this.modelExpense = new Model('Expense',requestService);
   }


  initObject() {

    this.expense = {
      user_id : this.authUser.user.id,
      amount : 0,
      team_id : this.team_id,
      date : new Date().toISOString()
    }
    console.log('new expense',this.expense);
    //this.addUser();
  }




  async submit(){

    await this.dialogService.loadingProgres();

    console.log('submit');
    this.modelExpense.api_create(this.expense).subscribe((respose) => {
      this.dialogService.dimissLoaging();
      if(respose['status'] == 'success'){
        
        this.dimiss(respose['Expense']);
      }
    },
    error => {
      this.dialogService.dimissLoaging();
    },()=>{
      this.dialogService.dimissLoaging();
    })
  }


  dimiss(expense){
    this.modalController.dismiss({
      expense : expense
    })
  }

  close() {
    this.modalController.dismiss();
  }

  log(event){
    console.log(event)
  }



}
