import { Injectable } from '@angular/core';
import { UtilService } from './util.service';

//import {html2canvas} from 'html2canvas';

var html2canvas = require('html2canvas');
 
@Injectable({
  providedIn: 'root'
})
export class HtmlToImageService {

  public img = '';

  constructor(
    public util : UtilService
  ) { }


  async run(id){
    console.log('run')
    const element = document.getElementById('html2canvas');
    const targetElement = document.getElementById(id).cloneNode(true);

    var offsetHeight = document.getElementById(id).offsetHeight;
    var offsetWidth = document.getElementById(id).offsetWidth;
    element.style.height = offsetHeight + 'px';
    element.style.width = offsetWidth + 'px';


    element.appendChild(targetElement);

    this.util.delay(100);

    let options = {
      letterRendering: 1, 
      allowTaint : true,
      logging:true,
      useCORS: true,
      onrendered : function (canvas) { 
        console.log('canvas',canvas);
      }
    }

    await html2canvas(element.firstChild,options).then((resp) => {
      //img;
      this.img =  resp.toDataURL("image/png") ;
      //console.log('resp' , resp);
      element.firstChild.remove();
    }).catch((res) => {
        console.log(res);
        element.firstChild.remove();

    });
  }

  dowload(name = '_'){
    var a = document.createElement("a"); //Create <a>
    a.href = this.img;
    a.download = `partido${name}.png`; //File name Here
    a.click();
  }


  get src(){
    return this.img ? this.img : false;
  }

}

