import { Injectable } from '@angular/core';
import { UtilArrayService } from './util-array.service';

@Injectable({
  providedIn: 'root'
})
export class ListsHomeService {


  public results = [];
  public games = [];
  public players = [];
  public teams = [];
  public publications = [];

  constructor(
    private utilArray : UtilArrayService
  ) { 
  }


  remove(nameList, id){
    if(this[nameList]){
      this.utilArray.listDelete(this[nameList],id);
    }
  }

  update(nameList,item){
    if(this[nameList]){
      this.utilArray.listUpdate(this[nameList],item.id,item);
    }
  }
}
